body {
	background-color: #172b4d;
}

input {
	background-color: #fff !important;
	border-radius: 44px !important;
	width: 90% !important;
	padding: 0px 15px !important;
}

input:focus {
	border-bottom: none !important;
	box-shadow: none !important;
}

label {
	display: block;
	color: #fff !important;
	font-size: 1rem !important;
}

.calculate-btn {
	background-color: #0c0c4f;
	padding: 15px 50px;
	color: white;
	font-size: 16px;
	border-radius: 44px;
	cursor: pointer;
	border: 1px solid #0c0c4f;
	margin-bottom: 40px;
	transform: translate3d(0, 0, 0);
	transition: all 0.2s ease;
}

.calculate-btn:hover {
	background-color: #fff;
	transform: translate(0px, -2px);
	color: #5364c3;
	box-shadow: 0px 15px 30px -12px rgba(255, 255, 255, 0.2);
}

.calculate-btn:focus {
	background-color: #32408f;
}

.calculate-btn:focus:hover {
	color: white;
}

.calculate-btn:disabled {
	border: 1px solid #999999;
	background-color: #cccccc;
	color: #666666;
	cursor: default;
}

.calculate-btn:disabled:hover {
	box-shadow: none;
	transform: translate(0, 0);
}

.data-container {
	background-color: #1f3a67;
	border-radius: 11px;
	margin-top: 40px;
	padding-top: 40px;
	padding-bottom: 40px;
}

.card {
	background-color: #0c0c4f !important;
	color: white;
}

.card-title {
	font-weight: 500 !important;
	text-align: center;
}

.card-data {
	display: grid;
	justify-content: space-around;
}

.delete-btn {
	background-color: #ec796b;
	color: white;
	border: none;
	border-radius: 50%;
	font-weight: 700;
	padding: 5px 9px;
	cursor: pointer;
	position: absolute;
	top: -12px;
	right: -12px;
}

.delete-btn:focus {
	background-color: #ec796b;
}
